/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

body {
  color: #000;
  background: #fff;
  font-family: "Open Sans", Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
  line-height: 1.33333;
}

.out {
  overflow: hidden;
}

.inner {
  position: relative;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1170px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.title-md {
  font-size: 32px;
  font-weight: 400;
  line-height: 41px;
  color: #fff;
  letter-spacing: -0.02em;
}

.title-md_gray {
  color: #355576;
}

.title-xs {
  font-size: 13px;
  font-weight: 600;
  line-height: 23px;
  letter-spacing: -0.02em;
  color: #3c76c7;
}

.btn {
  display: inline-block;
  background: #9cddfa;
  border-radius: 3px;
  padding: 18px 38px;
  color: #355576;
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: -0.01em;
  min-width: 245px;
  line-height: 24px;
}

.header {
  padding: 33px 0;
}

.header__inner {
  position: relative;
}

.logo {
  position: relative;
  z-index: 10;
  float: left;
  font-size: 0;
}

.logo img {
  width: 100%;
  display: block;
}

.btn-nav {
  position: absolute;
  z-index: 10;
  display: none;
}

@media only screen and (max-width: 767px) {
  .btn-nav {
    display: block;
  }
}

.btn-nav.is-active span {
  width: 19px;
}

.btn-nav span {
  position: absolute;
}

.btn-nav span:nth-child(1) {
  transform-origin: left top;
}

.btn-nav span:nth-child(2) {
  transform-origin: left center;
}

.btn-nav span:nth-child(3) {
  transform-origin: left bottom;
}

.nav {
  float: right;
}

.selection {
  background: #1e8bf0;
  padding: 55px 0 80px;
  text-align: center;
}

.selection__title {
  max-width: 700px;
  margin: 0 auto 25px;
}

.selection__description {
  font-size: 16px;
  font-weight: 400;
  color: #d2e7fb;
  letter-spacing: -0.02em;
  margin: 0 0 31px;
  line-height: 25px;
}

.feedback__title {
  text-align: center;
  margin-bottom: 80px;
}

.feedback__gallery {
  display: inline-block;
  margin: 0 -15px;
}

.feedback__item {
  display: inline-block;
  padding: 0 15px;
}
